import React from 'react'
import  { graphql, useStaticQuery } from "gatsby"

const query = graphql`
    {
        allContentfulContact {
            nodes {
                location
                phone1
                phone2
                email1
                email2
                horrios {
                    horrios
                }
            }
        }
    }
`

const ContactInfo = () => {
const contact = useStaticQuery(query).allContentfulContact.nodes[0]

    return (
        <div className="contact-info-area ptb-100 bg-f4f7fe">
            <div className="container">
                <div className="row">
                    <div className="col-lg-4 col-md-6">
                        <div className="contact-info-box">
                            <div className="icon">
                                <i className='bx bx-map'></i>
                            </div>
                            <h3>A nossa Localização</h3>
                            
                            <p>{contact.location}</p>
                        </div>
                    </div>

                    <div className="col-lg-4 col-md-6">
                        <div className="contact-info-box">
                            <div className="icon">
                                <i className='bx bx-phone-call'></i>
                            </div>
                            <h3>Contacto</h3>
                            <p>Telefone(s): <a href={`tel:${contact.phone1}`}>{contact.phone1}</a></p>
                                            <a href={`tel:${contact.phone1}`}>{contact.phone2}</a>
                            <p>E-mail(s): <a href={`mailto:${contact.email1}`}>{contact.email1}</a></p>
                                          <a href={`mailto:${contact.email2}`}>{contact.email2}</a>
                        </div>
                    </div>

                    <div className="col-lg-4 col-md-6">
                        <div className="contact-info-box">
                            <div className="icon">
                                <i className='bx bx-time-five'></i>
                            </div>
                            <h3>Funcionamento</h3>
                            <p>{contact.horrios.horrios}</p>             
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ContactInfo