import React, {useState} from 'react'
import contact from '../../assets/images/contact-dog.jpg'
import { send,init } from 'emailjs-com'
import {toast, Toaster} from "react-hot-toast"

init("user_88H22eLuawYUmXVmbEYqk")

const ContactForm = () => {
    const [toSend, setToSend] = useState({
        from_name: '',
        to_name: '',
        message: '',
        reply_to: '',
        info: ''
    });

    const onSubmit = (e) => {
        e.preventDefault();
        send(
          'service_19vc7se',
          'template_ampcumg',
          toSend,
          'user_88H22eLuawYUmXVmbEYqk',
        )
        .then((res) => {
        })
        .catch((err)=> {
        })
    }

    const handleChange = (e) => {
        setToSend({...toSend, [e.target.name]: e.target.value});
    };

    const msg = () => {
        toast.success("O seu Email foi enviado")
    }

    return (
        <section className="contact-area ptb-100">
            <div className="container">
                <div className="section-title">
                    <h2>Está Pronto a Iniciar o Treino do seu Cão ?</h2>
                    <p>O seu endereço de email não será publicado ou partilhado. Os campos necessários estão marcados com *</p>
                </div>

                <div className="row">
                    <div className="col-lg-6 col-md-12">
                        <div className="contact-image">
                            <img src={contact} alt="contact" />
                        </div>
                    </div>

                    <div className="col-lg-6 col-md-12">
                        <div className="contact-form">
                            <form id="contactForm" method="POST" onSubmit={onSubmit}>
                                <div className="row">
                                    <div className="col-lg-12 col-md-6">
                                        <div className="form-group">
                                            <input 
                                                type="text" 
                                                name="from_name" 
                                                className="form-control" 
                                                required 
                                                placeholder="O Seu Nome"
                                                value={toSend.from_name}
                                                onChange={handleChange}
                                            />
                                        </div>
                                    </div>

                                    <div className="col-lg-12 col-md-6">
                                        <div className="form-group">
                                            <input 
                                                type="email" 
                                                name="reply_to" 
                                                className="form-control" 
                                                required 
                                                placeholder="exemplo@exemplo.com"
                                                value={toSend.reply_to}
                                                onChange={handleChange}
                                            />
                                        </div>
                                    </div>

                                    <div className="col-lg-12 col-md-12">
                                        <div className="form-group">
                                            <input 
                                                type="text" 
                                                name="info" 
                                                className="form-control" 
                                                required 
                                                placeholder="O seu telefone"
                                                value={toSend.info}
                                                onChange={handleChange}
                                            />
                                        </div>
                                    </div>

                                    <div className="col-lg-12 col-md-12">
                                        <div className="form-group">
                                            <textarea 
                                                name="message" 
                                                className="form-control" 
                                                cols="30" 
                                                rows="6" 
                                                required 
                                                placeholder="Escreva a sua mensagem..."
                                                value={toSend.message}
                                                onChange={handleChange}
                                            />
                                        </div>
                                    </div>
                                    <Toaster />
                                    <div className="col-lg-12 col-md-12">
                                        <button type="submit" 
                                                className="default-btn" 
                                                onClick={msg}>
                                            <i className="flaticon-tick"></i> 
                                            Enviar Mensagem <span></span>
                                        </button>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default ContactForm